
.input-group{
  width: 120px;
}
.input-group input{
  text-align: center;
}
.input-group span{
  cursor: pointer;
}
.absolute{
  position: absolute;
}
.addCart-leave-active {
  -webkit-animation: addCart 0.5s ease-out;
          animation: addCart 0.5s ease-out;
}
@-webkit-keyframes addCart {
0% {
    -webkit-transform: scale(1) translate(0,0);
            transform: scale(1) translate(0,0);
    -webkit-transform-origin: left top;
            transform-origin: left top;
}
100% {
    -webkit-transform: scale(0) translate(-100px,-100px);
            transform: scale(0) translate(-100px,-100px);
    -webkit-transform-origin: left top;
            transform-origin: left top;
}
}
@keyframes addCart {
0% {
    -webkit-transform: scale(1) translate(0,0);
            transform: scale(1) translate(0,0);
    -webkit-transform-origin: left top;
            transform-origin: left top;
}
100% {
    -webkit-transform: scale(0) translate(-100px,-100px);
            transform: scale(0) translate(-100px,-100px);
    -webkit-transform-origin: left top;
            transform-origin: left top;
}
}
.pomegranage {
  color: #c0392b;
}
.detail dt {
  color: #95a5a6;
  width: 70px;
  padding: 4px 0;
}
.detail dd {
  margin-left: 64px;
  padding: 4px;
}
.option ul {
  padding: 0;
  list-style: none;
}
.option ul > li {
  float: left;
  margin-right: 4px;
  padding: 0 8px;
  border: 2px solid #eee;
}
.option ul > li:hover {
  border: 2px solid #c0392b;
  cursor: pointer;
}
.option ul .active {
  border: 2px solid #c0392b;
}



